@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Lato', sans-serif;
  padding: 21vmin 0 10vmin 0;
}

a:hover {
  background-color: yellow;
  color: black;
  text-decoration: none;
}

body {
  color: black;
  background-color: #FFFFFF;
  font-size: 3vmin;
  line-height: 1em;
}

p a {
  color:black;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  -webkit-text-decoration-style: dotted;
          text-decoration-style: dotted;
  text-decoration-thickness: 1px;
  text-underline-offset: .3em;
}

h1 {
  font-family:  'Bebas Neue', sans-serif;
  font-size:  10vmin;
  letter-spacing: 1px;
}

h2 {
  font-family:  'Bebas Neue', sans-serif;
  font-size:  10vmin;
  font-weight: lighter;
}

h3 {
  font-family:  'Bebas Neue', sans-serif;
  font-size:  5vmin;
  line-height: 5vmin;
  opacity: 30%;
  padding: .75vmin .75vmin 0 .75vmin;
  letter-spacing: 1px;
}

button {
  background: none;
  border: none;
  font-family: 'Lato', sans-serif;
  cursor: pointer;
}

.Section {
  line-height: 1em;
  font-family:  'Bebas Neue', sans-serif;
  font-size:  10vmin;
  margin: 10vmin;
}

span:hover {
  background-color: yellow;
}

@media (max-width: 460px) {
  .App {
      padding: 25vmin 0 10vmin 0;
  }
}
a {
    text-decoration: none;
  }

.navbar {
  background-color: black;
  color: white;
  top: 0;
  position: fixed;
  width: 100%;
  z-index: 1;
}

.navbarOptions {
  z-index: 2;
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navLeft {
  margin: 0 6vmin;
}

.navLeft h1 {
  color:white;
}

.navLeft h1:hover {
  color: yellow;
}

.navLink {
  margin: 1vmin;
  color: white;
}

.menuBar {
  width: 5vmin;
  height: 1vmin;
  background-color: white;
  margin: 1vmin;
}

.menuButton {
  background: none;
  border: none;
  font-family: 'Lato', sans-serif;
  padding: 6vmin;
}

.menuButton a {
  color:white;
}

.menuButton a:hover {
  color:black;
}

#dropdown {
  z-index: 1;
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  display: flex;
  align-items: center;
}

.menuItems {
  display: flex;
  flex-direction: column;
}

.navLink:hover {
  background-color: yellow;
  color:black;
}

.navLink {
  margin: 3vmin 7vmin;
  text-align: left;
  font-size: 5vmin;
  line-height: 5vmin;
}

.navSublink:hover {
  background-color: yellow;
  color:black;
  cursor: pointer;
}

.navSublink {
  margin: 3vmin 7vmin;
  text-align: left;
  font-size: 5vmin;
  line-height: 5vmin;
}

/* -1vmin left margin bc it's offset for... button... reasons */
