  margin: 3vmin 7vmin 3vmin 6vmin;
  text-align: left;
  font-size: 5vmin;
  line-height: 5vmin;
  color: white;
}

.navLinkButton:hover {
  color: black;
  background-color: yellow;
} */

/* FOR SMALLER SCREEN SIZES */

@media (max-width: 460px) {
  .navLeft h1 {
    font-size: 15vmin;
}

  .menuBar {
    width: 7.5vmin;
    height: 1.5vmin;
    margin: 1.5vmin;
  }

  .navLink {
    margin: 5vmin 10vmin;
    text-align: left;
    font-size: 7vmin;
    line-height: 7vmin;
  }

}
@media (max-width: 460px) {
  .Footer p {
    font-size: 4vmin;
  }
}

.Footer {
    background-color: black;
    color: white;
    position: fixed;
    bottom: 0;
    padding: 0.5vmin;
    font-size: 2.5vmin;
    width: 100%;
}

.footerLink a {
  color:yellow;
}

.footerLink a:hover {
  color:black;
}
.AllVideos {
    padding: 0vmin 0 10vmin 0;
}

.VideoList{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
}

.GigWrapper {
    padding: 2vmin 0;
    height: 25vmin;
    width: 70vmin;
    display: flex;
}

.VideoImg{
    height: 100%;
}

.VideoImg img {
    border-radius: 50px;
    height: 100%;
}

.VideoImg img:hover{
    opacity: 0.5;
}

.VideoImg a {
  background-color: transparent;
}

.TextBox {
    padding: 0 0 0 3vmin;
    height: 100%;
    text-align: left;
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.TextBox a {
    color: black;
}

.TextBox h1 {
    font-size: 6vmin;
    text-align: left;
    margin: 1vmin 0 0.5vmin 0;
}

.TextBox p {
    font-size: 2.5vmin;
    text-align: left;
    margin: 0;
    padding: 0.5vmin 0 1vmin 0;
}
.image-card {
    width: 25vmin;
    height: 25vmin;
    object-fit: cover;
    border-radius: 25%;
    padding: 1vmin;
}

.image-card:hover {
    opacity: 0.5;
}

#Gallery {
    padding: 0vmin 0 10vmin 0;
}

#LightboxImage {
    max-width: 70%;
    max-height: 90%;
}

#Lightbox {
    z-index: 1;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#Lightbox button {
    background-color: rgba(255, 255, 255, 0);
    color: white;
    border: none;
    padding: 2vmin;
    font-size: 8vmin;
}

#Lightbox button:hover {
    opacity: 0.5;
}

.genButton {
    margin: 0.3em;
    padding: 3vmin;
    font-family:  'Bebas Neue', sans-serif;
    font-size:  5vmin;
    border-radius: 0px;
    border: transparent;
    background-color: #D9D9D9;
}

.genButton:hover{
    background-color: yellow;
}

.StoryGen {
    margin: 1em 7vmin;
    font-size: 3vmin;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.Credits {
    font-size: 2.5vmin;
}
.Arrows {
    font-size: 4vmin;
    /* stops the dimensions from changing when the arrow changes */
    width: 4vmin;
    margin: 0 1vmin 0 0;
}
.Directories {
    margin: 0 0 5vmin 0;
}

.Directory {
    margin: 0 20vmin 10vmin 0;
}

.Directory h1 {
    text-align: center;
}

.Directory h2 {
    text-align: start;
    margin: 7vmin 0 0 14vmin;
    font-size: 8vmin;
    display: flex;
    align-content: center;
}

.Dropdown {
    cursor: pointer;
}

.FilterMenu {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 15vmin;
    height: 6vmin;
}

h3:hover {
    background-color: yellow;
    opacity: 100%;
    cursor: pointer;
}

.FilterSentence {
    font-family:  'Bebas Neue', sans-serif;
    opacity: 60%;
    padding: .75vmin .75vmin 0 .75vmin;
}

.Header {
    line-height: 0;
}

.Name {
    margin: 0 0 0 20vmin;
    text-align: start;
}

.Name a {
    text-decoration: none;
}

.Info {
    margin: 0 0 2vmin 1.25em;
}

@media (max-width: 460px) {
    .Directories p {
        font-size: 5vmin;
        line-height: 5vmin;
    }
    
    .Directories h1 {
        font-size: 15vmin;
    }

    .Directories h2 {
        font-size: 10vmin;
    }

    .Directory {
        margin: 0 10vmin 15vmin 0vmin;
    }

  }
